.artplayer-video-player {
  position: relative;
  width: 100%;
  height: 100%;
  outline: 0;
  font-family: Roboto,Arial,Helvetica,sans-serif;
  color: #eee;
  text-align: left;
  direction: ltr;
  font-size: 14px;
  line-height: 1.3;
  user-select: none;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  touch-action: manipulation;
  -ms-high-contrast-adjust: none; }
  .artplayer-video-player * {
    margin: 0;
    padding: 0;
    box-sizing: border-box; }
  .artplayer-video-player .art-icon {
    display: inline-block;
    line-height: 1.5; }
    .artplayer-video-player .art-icon svg {
      fill: #fff; }
